*{
  margin: 0;
  padding: 0;
  font-size: 20px;
}
h1{
  text-align: center;
  font-size: 40px;
}
div{
  text-align:left;
}
:root{
    --facebook: #3b5999;
    --instagram:#e4405f;
    --twitter: #55acee;
}
.Hnav{
    position: fixed;
    width: 100%;
    height: 80px;
    background-color: rgb(251, 251, 251);
    display: flex;
    z-index: 1000;
}
.Hnav .Hnavlogo, .Hnav .Hnavlogo img{
    height: 80px;
    width: 80px;
}
.HnavDiv{
    height: 80px;
    margin: auto;
}
.HnavList{
    height: 100%;
    width: 100%;
    display: flex;
}
.HnavItem{
    height: 100%;
    padding-right: 40px;
    margin: 0;
    list-style: none;
    padding-top: 10px;
}
.HnavLink{
    line-height: 50px;
    text-decoration: none;
    color: black;
}

.Hdropdown{
    display: none;
    text-align: left;
    position: absolute;
    top: 75px;
    width: 150px;
    list-style: none;
    background-color: rgb(247, 247, 247);
    border-radius: 0 3% 3% 3%;
    padding-left: 10px;
}
.HdropdowsItem{
    padding-top: 5px;
}
.HdropLink{
    text-decoration: none;
    color: #000;
}
.HnavItem:hover .HnavLink{
    color: #548aff;
}
.HnavItem:hover > ul{
    display: block;
}
.HdropdowsItem:hover .HdropLink{
    color: #548aff;
}
.HdropdowsItem:hover > ul{
    display: block;
}
.Hicons{
    width: 190px;
    margin: auto 0;
    padding-top: 15px;
}
.HiconList:hover a{
    -webkit-filter: blur(2px);
            filter: blur(2px)
}
.HiconList{
    display: flex;
    padding: 0 10px 0 0;
}
.Hicon{
    margin: auto;
    list-style: none;
    text-align: center;
    transition: 0.2s;
}
.Hicon a{
    width: 50px;
    border-radius: 50%;
    display: block;
    text-decoration: none;
    color: black;
    line-height: 50px;
}
.Hicon a:hover{
    -webkit-transform: translateY(-3px) scale(1.05);
            transform: translateY(-3px) scale(1.05);
    box-shadow: 0 5px 5px 0px rgba(0, 0, 0, .4);
    -webkit-filter: none;
            filter: none;
    color: #fff;
}
.Hfacebook:hover{
    background: #3b5999;
    background: var(--facebook);
}
.Hinstagram:hover{
    background: #e4405f;
    background: var(--instagram);
}
.Htwitter:hover{
    background: #55acee;
    background: var(--twitter);
}

.Huser{
    width: 50px;
}
.HuserList{
    display: block;
    margin: auto;
    list-style: none;
    padding: 0;
}
.HuserList a{
    font-size: 25px;
    color: #3b5999;
}

.Hcart{
    display: block;
    width: 60px;
    margin: auto 0;
}
.HcartList{
    padding: 0;
    margin: auto;
    list-style: none;
    width: 55px;
    height: 30px;
    text-align: center;
}
.Hbuy-a{
    font-size: 25px;
    width: 48px;
    height: 30px;
    border-radius: 50%;
    display: flex;
    text-decoration: none;
    color: #000;
    line-height: 45px;
}
.Hrow-product{
    max-height: 320px;
    overflow-y: scroll;
}
.Hrow-product::-webkit-scrollbar {
    width: 3px;
}
.Hrow-product::-webkit-scrollbar-thumb{
    background-color: #c4c4c4;
    border-radius: 50px;
}
.Hrow-product::-webkit-scrollbar-thumb:hover{
    background-color: #a5a5a5;
}
.Hcount-products{    
    background-color: #000;
    color: #fff;
    padding: 9px;
    margin-top: auto;
    height: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
}

#Hcontador-productos{
    font-size: 12px;
}

.Hcontainer-cart-products{
    position: absolute;
    top: 80px;
    right: 0;
    background-color: #fff;
    width: 400px;
    z-index: 1;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.20);
    border-radius: 0 0 10px 10px;
}

.Hcart-product{
    display: flex;
    justify-content: space-between;
    padding: 20px 30px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.20);
}

.Hinfo-cart-product{
    display: flex;
    justify-content: space-between;
    flex: 0.9 1;
}

.Htitulo-producto-carrito{
    font-size: 20px;
    margin: auto 0;
}

.Hprecio-producto-carrito{
    font-weight: 700;
    font-size: 20px;
    margin-left: 10px;
    margin: auto 0;
}

.Hcantidad-producto-carrito{
    font-weight: 400;
    font-size: 20px;
    margin: auto 0;
}

.Hicon-close{
    width: 25px;
    height: 25px;
    margin: auto 0;
}

.Hicon-close:hover{
    stroke: red;
    cursor: pointer;
}

.Hcart-total{
    display: flex;
    justify-content: center;
    padding: 15px 0;
    grid-gap: 20px;
    gap: 20px;
}

.Hcart-total h3{
    font-size: 20px;
    font-weight: 700;
    margin: auto 0;
}

.Htotal-pagar{
    font-size: 20px;
    font-weight: 900;
}

.Hhidden-cart{
    display: none;
}

.Hcart-empty{
    padding: 20px 0 0 0;
    text-align: center;
}
.Hbtn-clear-all{
    border: none;
    background-color: #000;
    color: #fff;
    padding: 15px 0;
    display: block;
    width: 100%;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    font-family: inherit;
    cursor: pointer;
    font-size: 16px;
    transition: all 0.3s ease;
    margin: 0;
}
.Hbtn-clear-all:hover{
    background-color: #515151;
    -webkit-transform: scale(1.03);
            transform: scale(1.03);
    border-radius: 10px;
}

@media screen and (max-width: 1030px){
    .Hbars__menu{
        width: 30px;
        height: 30px;
        margin: auto 10px auto 0;
        cursor: pointer;
    }
    .Hbars__menu span{
        display: block;
        width: 100%;
        height: 2px;
        background-color: #000;
        margin-top: 6px;
        transition: all .5s;
    }
    .Hline2__bars-menu{
        position: relative;
        left: 0;
    }
    .HactiveLine1__bars-menu{
        -webkit-transform: rotate(45deg) translate(-2px, 1px);
                transform: rotate(45deg) translate(-2px, 1px);
        -webkit-transform-origin: 0px 100%;
                transform-origin: 0px 100%;
        translate: 5px;
    }
    .HactiveLine2__bars-menu{
        opacity: 0;
        position: relative;
        left: 30px;
    }
    .HactiveLine3__bars-menu{
        -webkit-transform: rotate(-45deg) translate(-4px, 2px);
                transform: rotate(-45deg) translate(-4px, 2px);
        -webkit-transform-origin: 0px 100%;
                transform-origin: 0px 100%;
        translate: 5px;
    }
    .HnavList{
        display: block;
        position: absolute;
        right: -100vw;
        transition: all .5s;
        top: 80px;
        height: calc(100vh - 80px);
        padding: 0;
        border-radius: 0 0 0 100%;
        text-align: center;
        width: 100%;
        padding-top: 10vh;
    }
    .HactiveNavList{
        background-color: #000;
        border-radius: 0 0 0 50%;
        translate: -100vw;
    }
    .HnavItem{
        margin: auto;
        height: 70px;
        width: 190px;
        list-style: none;
        padding: 0;
    }
    .HnavLink, .HdropLink{
        color: #fff;
    }
    .HactiveDropdown{
        display: none;
        text-align: left;
        position: relative;
        top: 0;
        left: 50px;
        width: 150px;
        list-style: none;
        background-color: transparent;
        border-radius: 0 3% 3% 3%;
        padding: 0;
        margin: auto;
    }
}
@media screen and (max-width: 600px){
    .Hnav{
        height: 40px;
        transition: all .3s;
    }
    .HnavUp{
        -webkit-transform: translateY(-40px);
                transform: translateY(-40px);
    }
    .HnavDiv{
        height: 40px;
    }
    .Hnav .Hnavlogo, .Hnav .Hnavlogo img{
    height: 40px;
    width: 40px;
    }
    .HnavList{
        top: 40px;
        height: calc(100vh - 40px);
    }
    .Hcart{
        margin-right: 10px;
    }
    .Hrow-product{
        max-height: 250px;
    }
    .Hcontainer-cart-products{
        top: 40px;
        width: 100%;
    }
    .Hicons{
        width: 0;
    }
    .HactiveIcons{
        width: auto;
        margin: 0;
        padding: 0;
    }
    .HiconList{
        position: absolute;
        top: 55px;
        right: -100vw;
        transition: all 0.3s;
    }
    .HactiveIconList{
        right: 20px;
    }
    .HactiveIcon a{
        color: #fff;
    }
    .Huser{
        margin-right: 10px;
        display: flex;
    }
    .HuserList{
        display: flex;
        margin: auto 0;
    }
    .Hlogin{
        margin-right: 10px;
    }
}
@media screen and (max-width: 300px) {
    .Hcart-product{
        padding: 20px 5px;
    }
    .Hinfo-cart-product{
        flex: .95 1;
    }
}


html, .body{
    margin: 100;
    padding: 100;
}

.body{
