    font-family: sans-serif;
    letter-spacing: 0.03em;
    line-height: 1.6;
    background-image: url(/react-proyecto/build/static/media/fondo.f8fdf4f1.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

title, h1{
    text-align: center;
    font-size: 40px;
    background-color: rgb(255, 255, 0);
    color: blue;
    margin-top: 0px;
    font-weight: 100;
}


.container{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: auto;
}

.container .card{
    width: 200px;
    height: 250px; /* comentario pruebaa*/
    border-radius: 8px;
    box-shadow: 0 2px 2px rgba(228, 53, 53, 0.2);
    overflow: hidden;
    margin: 20px;
    text-align: center;
    transition: all 0.25s;

}

.container .card:hover{
    -webkit-transform: translateY(-15px);
            transform: translateY(-15px);
    box-shadow: 0 12px 16px yellowgreen
}

.principal {
    font-size: 12px;
    height: 300px;
}

img{
    width: 100px;
    height: 100px;
}

.card button{
    border: none;
    background: none;
    background-color: #000;
    color: #fff;
    padding: 15px 10px;
    cursor: pointer;
}
